export default {
	data() {
		return {
			oldpassword:"",
			newpassword:"",
			renewpassword:""
		}
	},
	computed: {
		disabled() {
			return this.oldpassword == '' || this.newpassword == '' || this.renewpassword == '' 
		}
	},
	methods: {
		// 验证
		check(){
			if (this.newpassword !== this.renewpassword) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none'
				});
				return false
			}
			return true
		},
		// 提交
		submit(){
			if (!this.check()) {
				return;
			}
			console.log("提交成功")
		}
	}
}
